.ggb-image-slider {
	position: relative;
	overflow: hidden;
	width: 100%;

	&.alignwide {
		.ggb-image-slider-track {
			margin-left: auto;
			margin-right: auto;
			max-width: 1100px;
			box-sizing: border-box;
		}
	}

	.ggb-image-slider-track {
		display: flex;
		transition: transform 0.8s ease-in-out;
		width: 100%;
	}

	.slider-image-wrapper {
		flex: 0 0 100%;
		max-width: 100%;
		overflow: hidden;
		position: relative;
		height: 100vh;
	
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	
	.ggb-image-slider-preview {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
		gap: 1rem;

		.slider-image-wrapper {
			height: auto;
		}
	}
}

.wp-block-column .alignfull.ggb-image-slider, .wp-block-column .u-full-bleed.ggb-image-slider {
    margin: 0 auto;
}
